// //popup css are started here
.MuiDialogContent-root:has(.woo-image-overlay) {
  &::-webkit-scrollbar {
    width: 0;
  }
}

.woo-image-overlay {
  .woo-overlay-content {
    background: #fff;
    padding: 0rem 1rem;
    text-align: center;
    border-radius: 0.25rem;
    max-width: 26rem;
    position: relative;

    .woo-modal.cross {
      position: absolute;
      right: 1.5rem;
      top: 1.5rem;
      color: #181819;
    }
  }

  h1.banner-header {
    font-size: 2rem;

    span.banner-pro-tag {
      line-height: 0;
      background: #65438f;
      color: #f9f8fb !important; // to adjust pro tag text color
      padding: 0 0.25rem;
      border-radius: 0.25rem;
      margin-left: 0.125rem;
      box-shadow: 0.25rem 0.25rem 0.875rem rgb(63 20 115 / 25%);
    }
  }

  h2 {
    font-size: 1.5rem;
    margin: 1rem 0 1.5rem;
    font-weight: 400;
  }

  h3 {
    color: #181819;
    font-weight: 600;
    margin-bottom: 1rem;
  }



  .discount-tag {
    background: #fbedee;
    color: #e26f76;
    font-size: 0.875rem;
    padding: 0.315rem 1.25rem;
    width: fit-content;
    margin: 0.5rem auto 1rem;
    border-radius: 2rem;

    b {
      font-weight: 600;
    }
  }

  p {
    color: #5e5d60;
    font-size: 1rem;
  }

  .woo-banner-offer {
    margin: 1.5rem 0 1rem;
    font-size: 1.25rem;
    font-weight: 700;
    color: #181819;
  }

  .upgrade {
    color: #181718;
  }

  .woo-banner-content {
    font-size: 1rem;
    line-height: 1.6;
    letter-spacing: 0.02rem;
    font-weight: 500;

    p {
      font-size: 0.9rem;
      margin: 0 0 0.25rem;
      text-align: left;
      letter-spacing: 0;
      font-weight: 400;
      font-style: italic;
      max-width: 50%;
      margin: auto;
    }

    #description {
      font-size: 1rem;
      line-height: 1.6;
      letter-spacing: .02rem;
      font-weight: 500;
      text-align: center;
      max-width: 100%;
    }
  }

  a.woo-go-pro-btn {
    background: #e35047;
    color: #f9f8fb;
    text-decoration: none;
    padding: 0.5rem 1.5rem;
    line-height: 1.6;
    border-radius: 0.25rem;
    font-size: 1.25rem;
    margin: 2rem auto 0rem;
    display: block;
    width: fit-content;
    box-shadow: 0.25rem 0.25rem 1.5rem rgb(227 80 71 / 50%);
  }
}

.MuiPaper-root {
  padding: 2rem;
}

.woo-module-dialog-title {
  text-align: center;
}

span.stock-manager-pro-tag {
  position: absolute;
  right: 0.5rem;
  background: #522c81;
  color: #fdfdfe;
  padding: 0.125rem 0.5rem;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 0.625rem;
  font-size: 0.625rem;
  text-align: center;
  line-height: 1;
  text-transform: capitalize;
}